.item {
    display:flex;
    padding:15px;
    background-color:#C2ADA1;
    border-top-right-radius: 30px;
    border-bottom-left-radius: 30px;
    border-top-left-radius: 3px;
    border-bottom-right-radius: 3px;
    width:290px;
    margin-right:15px;
}

.item p {
    margin:0;
    font-size: 1.2rem;
    font-weight: bold;
    color:#1E1D1E;
}

.time-stamp .btn button, .item-description button {
    margin:0 !important;
    background-color:#A5A4A5;
    border:none;
    font-size:1.6rem;
    width:50px;
    font-weight: bold;
    transition:200ms ease;
    border-top-right-radius: 5px;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
} .time-stamp .btn button:hover, .item-description button:hover {
    cursor: pointer;
    background-color:#8E8E8E;
}

.time-stamp {
    display:flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    text-align: center;
}

.list {
    display:flex;
    flex-direction: row;  
    overflow-x:auto;
  }